@import '../../CommonFiles/commonStyles.module';
.mainPageAuth {
   .imageWrapper {
      margin-top: 40px;
      display: flex;
      .imageLink {
         margin: 20px auto;
         background-color: #000;
         padding: 10px;
         border-radius: 10px;
         .networkName {
            color: #fff;
            font-size: 32px;
         }
      }
   }
   .authWrapper {
      box-shadow: $boxShadowForBlock;
      margin: 0 auto;
      background-color: #fff;
      display: flex;
      justify-content: center;
      width: 400px;
      align-items: center;
      border-radius: 7px;
   }
}




